.app__aboutus {
    position: relative;
}

.app__aboutus-overlay {
    position: absolute;
    inset: 0;
}

.app__aboutus-overlay img {
    width: 441px;
    height: 465px;
    z-index: 0;
}

.app__aboutus-content {
    width: 100%;
    z-index: 2;
}

.app__aboutus-content_about {
    flex: 1;
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;
    
    text-align: right;
}

.app__aboutus-content_flower {
    margin: 2rem 4rem;
}

.app__aboutus-content_flower img {
    height: 400px;
}

.app__aboutus-content_history {
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
    
    text-align: left;
}

.app__aboutus-content_history p,
.app__aboutus-content_about p {
    margin: 2rem 0;
    color: var(--color-grey);
}

@media screen and (max-width: 2000px) {
    .app__aboutus-content_flower img {
        height: 400px;
    }

    .app__aboutus-content_history p,
    .app__aboutus-content_about p {
        margin: 4rem 0;
        color: var(--color-white);
    }
}

@media screen and (max-width: 1241px) {
    .app__aboutus-content_flower img {
        height: 300px;
    }
}

@media screen and (max-width: 1045px) {
    .app__aboutus-content {
        flex-direction: column;
    }

    .app__aboutus-content_flower {
        margin: 4rem 0;
    }
}

@media screen and (max-width: 650px) {
    .app__aboutus-content_flower img {
        height: 250px;
    }
}